.h100vh{height: 100vh!important;}

::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  background: #888;
}
::-webkit-scrollbar-thumb:hover {
  background: rgb(133, 112, 112);
}
.overflow-y-auto{
  overflow-y: auto!important;
}
.overflow-x-auto{
  overflow-x: auto!important;
}
.nav-overflow>ul{
  overflow: auto;
  overflow-x: hidden;
  height: calc(100vh - 46px);
}
.main-content{
  background-color: #f5f5f5!important;
  width: 100%!important;
  height: calc(100vh - 46px)!important;
  overflow: auto;
}
.req::after{
  content: ' *';
  color: red;
}

.content-data{
  width: 100%!important;
}
.text-bold{
  font-weight: bold;
}

.d-contents{
  display: contents!important;
}
.m-auto{ margin: auto!important; }
.ml-auto{ margin-left: auto!important; }
.mr-auto{ margin-right: auto!important; }
.mb-0{margin-bottom: 0px!important;}
.mt-0{margin-top: 0px!important;}
.mr-0{margin-right: 0px!important;}
.ml-0{margin-left: 0px !important;}
.page-description{
  background-color: teal!important;
  margin-top: 4px;
  height: 40px;
}

.form-group .ant-input, .form-group .ant-select {
  margin-bottom: 8px!important;
}
.form-group .ant-input.mb-0, .form-group .ant-select.mb-0 {
  margin-bottom: 0px!important;
}

.app-modal-body-overflow .ant-modal-body, .app-modal-without-footer-body-overflow .ant-modal-body {
  max-height: calc(100vh - 200px)!important;
  display: flex;
  flex-direction: column;
  overflow: auto!important;
}

.app-modal-without-footer-body-overflow .ant-modal-body{
  max-height: calc(100vh - 100px)!important;
}

form fieldset:disabled *{
  pointer-events: none;
}
form fieldset:disabled .ant-upload-picture-card-wrapper *{
  pointer-events: all;
}
form fieldset:disabled .ant-upload-picture-card-wrapper .ant-upload-list-item-card-actions-btn * {
  pointer-events: none!important;
  display: none;
}
.tox-notification.tox-notification--in.tox-notification--warning, .tox-statusbar__branding{
  display: none!important;
}

.ant-modal-fullscreen {
  width: 100vw!important;
  height: 100vh;
  top: 0px!important;
  max-width: none!important;
  margin: 0!important;
}

.ant-modal-fullscreen .ant-modal-body{
  height: calc(100vh - 110px)!important;
  display: flex;
  flex-direction: column;
  overflow: auto!important;
}

.ant-modal-fullscreen .ant-modal-content {
  height: 100vh;
  width: 100vw;
}

.ant-tabs-nav{
  margin-bottom: 6px!important;
}
.ant-tabs-content-holder{
  padding: 10px;
  overflow: auto;
  height: calc(100vh - 204px)
}
/* .ant-tabs-nav-wrap{
  background-color: #fafafa!important;
} */
.ant-tabs-tab{
  border-top: 2px solid transparent!important;
}
.ant-tabs-tab.ant-tabs-tab-active{
  border-top: 2px solid red!important;
}

.ant-message .anticon {
  top: -3px!important;
}

